.menu {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  position: sticky;
  top: 0;
  z-index: 100;
  background-color: white;
  width: 100%;
  box-shadow: 0px 0px 8px gray;

  img {
    max-height: 100%;
    height: 14vh;
    overflow: hidden;
  }

  .phoneMenu {
    display: none;
  }

  ul {
    display: flex;
    justify-content: center;
    padding: 2rem 0rem;
    margin: 0rem;

    li {
      margin: 0rem 5rem;
      list-style: none;

      .listElement {
        text-decoration: none;
        color: black;
      }

      .listElement:hover {
        color: gray;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .menu {
    height: 20vh;
    display: flex;
    justify-content: flex-end;
    align-items: center;

    .phoneMenu {
      padding: 1em;
      margin-right: 2em;
      display: flex;
      flex-direction: column;

      .phoneMenuDiv {
        background-color: black;
        margin: 0.1em 0em;
        width: 1.2em;
        height: 0.2em;
      }
    }

    ul {
      display: none;
    }
  }
}
