.contactMainForm {
  color: black;
  background-color: white;
  width: 65%;

  .contactUsDiv {
    display: flex;
    width: 100%;
    margin: 2em 0em;

    .askUsDiv {
      width: 30%;
      display: flex;
      justify-content: center;
      align-items: flex-start;

      p {
        padding-top: 50px;
        text-align: center;
        font-size: 1.5em;
        padding-bottom: 1em;
        border-bottom: 2px orange solid;
      }
    }

    .formDiv {
      width: 70%;
    }
  }
}
